<template>
  <div>
    <GalleryAlbumModal :isModal="isModal" @isModalFalse="isModalFalse" />
    <v-card class="gallery__album">
      <div class="gallery__album__top d-flex flex-row align-center justify-space-between">
        <v-card-title v-if="$store.state.isUser">
          Мои альбомы ({{ albums.length }})
        </v-card-title>
        <v-card-title v-else>Альбомы ({{ albums.length }})</v-card-title>
        <v-btn 
          v-if="$store.state.isUser" 
          color="myPurple" 
          style="margin-right: 30px; color: #fff;"
          @click.stop="isModal = true"
        >
          Создать альбом
        </v-btn>
      </div>
      <div 
        class="gallery__album__list d-flex flex-wrap flex-row justify-center" 
        v-if="albums != []"
      >
        <div v-for="(item, index) of albums" :key="index">
          <router-link 
            :to="{name: 'Album', params: { id: $route.params.id, album: item.id }}" 
            style="text-decoration: none"
          >
            <v-img 
              class="gallery__album__item"
              width="300"
              height="165"
              v-if="item.images[0]" 
              :src="'/' + item.images[0]"
            >
              <h2>{{ item.title }}</h2>
            </v-img>
          </router-link>
        </div>
      </div>
    </v-card>
  </div>
</template>

<script>
import GalleryAlbumModal from "./GalleryAlbumModal.vue";
import axios from "axios";

export default {
  name: "GalleryAlbum",
  components: { GalleryAlbumModal },
  data() {
    return {
      isModal: false,
      albums: []
    }
  },
  async created() {
    let res = await axios.get("/api/users/get_albums/" + this.$route.params.id)
    this.albums = res.data
  },
  methods: {
    isModalFalse(data) {
      this.isModal = data
    }
  }
};
</script>

<style lang="scss" scoped>
.gallery {
  &__album {
    min-height: 150px;
    &__list {
      margin-top: 30px;
    }
    &__item {
      color: #fff;
      width: 300px;
      height: 165px;
      margin-bottom: 30px;
      margin-right: 30px;
      margin-left: 30px;
      cursor: pointer;
      transition: 0.1s ease-in-out;
      h2 {
        background-color: #000;
        padding: 10px;
      }
    }
  }
}
</style>
