<template>
  <div>
    <NotAuth>
      <template v-slot:error></template>
      <template v-slot:loading></template>
      <template v-slot:default>
        <div class="d-flex flex-row">
          <div style="margin-top: 100px; margin-left: 20px;"> <Panel /> </div>
          <div class="gallery">
            <GalleryAlbum />
            <GalleryList :isUser="isUser" />
          </div>
        </div>
      </template>
    </NotAuth>
  </div>
</template>

<script>
import GalleryList from "../components/gallery/GalleryList.vue";
import GalleryAlbum from "../components/gallery/GalleryAlbum.vue";
import NotAuth from "../components/NotAuth.vue";
import Panel from "../components/Panel.vue";

export default {
  name: "Gallery",
  metaInfo: {
    title: "MyOcean - Моя галерея"
  },
  components: { GalleryList, GalleryAlbum, NotAuth, Panel },
  data() {
    return {
      user: "",
      isUser: false
    }
  },
  async created() {
    this.user = JSON.parse(localStorage.getItem("user"))
    if (this.user.id == this.$route.params.id) {
      this.isUser = true
    }
    this.$store.commit("isUserTrue", this.isUser)
  }
};
</script>

<style lang="scss" scoped>
.gallery {
  margin-top: 100px;
  width: 1170px;
}
</style>
