<template>
  <div>
    <v-dialog class="gallery__modal" persistent v-model="isModal" max-width="800">
      <v-card>
        <v-toolbar dark color="primary">
          <v-toolbar-title>Создать альбом</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn @click="$emit('isModalFalse', false)" icon dark>
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <div class="gallery__modal__form d-flex flex-column align-center">
          <v-text-field 
            solo 
            label="Введите название вашего альбома"
            class="gallery__modal__name"
            v-model="gallery.name"
          ></v-text-field>
          <v-textarea 
            solo 
            label="Введите описание вашего альбома"
            class="gallery__modal__description"
            v-model="gallery.description"
            auto-grow
          ></v-textarea>
        </div>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" @click="$emit('isModalFalse', false)">Отмена</v-btn>
          <v-btn color="success" @click="dataSend">Создать</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "GalleryAlbumModal",
  props: {
    isModal: Boolean
  },
  data() {
    return {
      gallery: {
        name: "",
        description: ""
      }
    }
  },
  methods: {
    async dataSend() {
      let user = JSON.parse(localStorage.getItem("user"))
      let res = await axios.put("/api/users/album_create/" + user.id, this.gallery)
      localStorage.setItem("user", JSON.stringify(res.data))
      this.$router.push({name: 'Album', params: 
        { id: this.$route.params.id, album: res.data.albums[0].id }})
      this.$emit("isModalFalse", false)
    }
  }
};
</script>

<style lang="scss" scoped>
.gallery {
  &__modal {
    &__form {
      margin-top: 30px;
    }
    &__name {
      width: 500px;
    }
    &__description {
      width: 500px;
    }
  }
}
</style>
