<template>
  <div>
    <v-card class="gallery__list">
      <div class="gallery__list__top d-flex flex-row align-center justify-space-between">
        <v-card-title v-if="galleryList">
          Мои фотографии ({{ galleryList.length }})
        </v-card-title>
        <v-card-title v-else>Мои фотографии (0)</v-card-title>
        <v-btn 
          color="myPurple" 
          v-if="$store.state.isUser" 
          style="margin-right: 30px; color: #fff;"
        >
          <label for="upload" v-if="$store.state.isUser">
            Добавить фотографию
            <input 
              v-if="$store.state.isUser" 
              @input="imageSave" 
              id="upload" 
              class="hide" 
              type="file" 
            />
          </label>
        </v-btn>
      </div>
      <div class="d-flex justify-center flex-row flex-wrap gallery__list__middle" v-if="galleryList">
        <div v-for="(gallery, index) of galleryList" :key="index">
          <GalleryImage 
            :isEdit="isEdit" 
            :isEditRed="isEditRed" 
            :isMaxLength="isMaxLength" 
            :gallery="gallery"
            @isEditData="isEditData" 
            @isEditRedData="isEditRedData" 
            @isMaxLengthData="isMaxLengthData"
          />
        </div>
      </div>
      <div class="d-flex flex-row justify-center align-center" v-if="!galleryList.length">
        <h2 v-if="isUser" class="display-1">Вы еще не загрузили ни одного изображения</h2>
        <h2 v-else class="display-1">
          Данный пользователь еще не загрузил ни одного изображения
        </h2>
        <v-img max-width="300" src="../../assets/error.jpg"></v-img>
      </div>
    </v-card>
    <v-snackbar v-model="isImage" :timeout="2000">
      <span>Изображение загружено. Для просмотра обновите страницу</span>
      <v-btn color="myPurple" style="color: #fff" @click="isImage = false">Закрыть</v-btn>
    </v-snackbar>
    <v-snackbar v-model="isMaxLength" :timeout="2000">
      <span>Описание не должно превышать 270 символов</span>
      <v-btn color="myPurple" style="color: #fff" @click="isMaxLength = false">
        Закрыть
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import GalleryImage from "./GalleryImage.vue";
import axios from "axios"
import "../../scss/galleryList.scss";

export default {
  name: "GalleryList",
  components: { GalleryImage },
  data() {
    return {
      isImage: false,
      isEdit: false,
      isEditRed: false,
      galleryList: [],
      isMaxLength: ""
    }
  },
  props: {
    isUser: Boolean
  },
  async created() {
    try {
      let res = await axios.get("/api/users/get_gallery/" + this.$route.params.id)
      this.galleryList = res.data
    } catch(err) {
      console.log(err.message)
    }
  },
  methods: {
    async imageSave(event) {
      let img = event.target.files[0]
      let formData = new FormData()
      formData.set("img", img, img.name)
      let res = await axios.put("/api/users/image_save/" + this.$route.params.id, formData,
      { headers: { "Content-Type": "multipart/form-data" }})
      localStorage.setItem("user", JSON.stringify(res.data))
      this.isImage = true
    }, 
    isEditData(data) { this.isEdit = data },
    isEditRedData(data) { this.isEditRed = data },
    isMaxLengthData(data) { this.isMaxLength = data }
  }
};
</script>
